.navbar {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
}
.navbar ul {
  list-style: none;
  display: flex;
  justify-content: space-evenly;
}
.navbar ul li {
  padding: 20px;
}
.navbar ul li a {
  text-decoration: none;
  padding: 20px;
  margin: 10px;
  font-size: 20px;
  font-weight: bold;
  color: tomato;
}
.navbar ul li a:hover {
  color: white;
  border-bottom: 3px solid white;
}
.hide {
  display: none;
}
.show {
  display: flex;
}
.fas {
  display: none;
  font-size: 2rem;
  margin: 2rem;
}
@media screen and (max-width: 760px) {
  .navbar ul {
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: none;
    margin-bottom: 2rem;
  }
  .hide {
    display: none;
  }
  .show {
    display: flex;
  }
  .fas {
    display: block;
  }
}
