.about {
  height: 100%;
  display: flex;
  flex-flow: column;
  margin: 0 2rem;
  height: 88vh;
}

.about-content {
  flex-grow: 1;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: 'two one three';
  grid-column-gap: 1rem;
  justify-content: center;
  align-items: center;
}
.about-item1 {
  grid-area: 'one';
  border: 4px dashed tomato;
  /* border-top: 4px dashed tomato;
  border-right: 4px dashed tomato; */
  padding: 1rem;
}
.about-item2 {
  grid-area: 'two';
  /* border-bottom: 4px dashed tomato; */
  padding: 1rem;
}
.about-item2 img {
  border-radius: 50%;
}
.about-item3 {
  grid-area: 'three';
  border: 4px dashed tomato;
  /* border-top: 4px dashed tomato;
  border-right: 4px dashed tomato; */
  padding: 1rem;
}
.about-item3 ul {
  list-style: none;
  margin: 1rem;
}

@media screen and (max-width: 760px) {
  .about-content {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    justify-content: center;
    align-items: center;
    margin: 1rem;
  }
  .about {
    margin: 0;
  }
}

/* End of about style */

.contact {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 88vh;
}
form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
form input,
textarea {
  display: block;
  background-color: transparent;
  padding: 1rem;
  border: none;
  border-bottom: 2px solid tomato;
}
input[type='submit'] {
  border: 2px solid tomato;
  cursor: pointer;
}
form input,
textarea,
input[type='submit']:focus {
  outline: none;
}

/* end of contact */
.products {
  height: 100%;
  margin: 1rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 1rem;
}
.products .item {
  /* height: 200px; */
  border: 2px solid tomato;
  background-image: url('https://via.placeholder.com/300');
  background-size: cover;
  border-radius: 1rem;
}

/* design for mobile */

@media screen and (max-width: 760px) {
  .products {
    grid-template-columns: 1fr;
  }
}

/* start of card */
.pro-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 2px solid tomato;
  border-radius: 1rem;
}
.pro-card img {
  border-radius: 1rem;
  padding: 10px;
}
.pro-card button {
  padding: 1rem;
  margin: 1rem;
  background-color: transparent;
  border: 2px solid tomato;
  border-radius: 10px;
  transition: 0.3s ease-in-out;
  cursor: pointer;
  color: rgb(107, 98, 98);
  font-family: 'Times New Roman', Times, serif;
  font-style: italic;
  font-weight: bolder;
}
.pro-card button:hover {
  transform: scale(1.1);
}
