.cover {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 88vh;
}
.card {
  width: 75%;
}
.card p {
  margin-bottom: 1rem;
}
hr {
  width: 60px;
  border: 2px solid white;
  color: white;
  margin: 1rem auto;
}
button {
  padding: 1rem;
  margin: 1rem;
  background-color: transparent;
  border: 2px solid tomato;
  border-radius: 10px;
  transition: 0.3s ease-in-out;
  cursor: pointer;
  color: rgb(107, 98, 98);
  font-family: 'Times New Roman', Times, serif;
  font-style: italic;
  font-weight: bolder;
}
button:hover {
  transform: scale(1.1);
}
.card {
  border: 1px darkgrey thin;
  box-shadow: 5px 5px 5px #333;
  padding: 2rem;
}

@media screen and (max-width: 760px) {
  .card {
    width: 95%;
  }
}
